  text-align: right;
  text-transform: uppercase;
  font-family: Gilroy;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.header__suppbox-language-li--active {
  color: #fcd54c;
  text-align: right;
  text-transform: uppercase;
  padding-right: 8px;
  font-family: Gilroy;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.menu__ul {
  flex-direction: column;
  gap: 20px;
  padding-left: 10px;
  display: flex;
}

.menu__ul-li {
  color: #fff;
  text-shadow: 0 4px 4px rgba(0, 0, 0, .25);
  text-transform: uppercase;
  font-family: Gilroy;
  font-size: 36px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.burger__open-buy {
  opacity: 0;
  height: 0;
  color: #fff;
  text-transform: none;
  gap: 15px;
  padding-top: 10px;
  padding-left: 30px;
  font-family: Gilroy;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  transition: opacity 1s;
  display: grid;
}

.menu__ul-li--down {
  height: 42px;
  transition: height 1s;
  position: relative;
  overflow: hidden;
}

.menu__ul-li--down:focus > .burger__open-buy, .menu__ul-li--down:hover > .burger__open-buy {
  height: auto;
  opacity: 1;
}

.menu__ul-li--down:focus, .menu__ul-li--down:hover {
  height: 132px;
}

.buy__images {
  margin-left: 18px;
  transition: transform 1s;
  transform: rotate(0deg);
}

.menu__ul-li--down:focus .buy__images, .menu__ul-li--down:hover .buy__images {
  transform: rotate(180deg);
}

.menu__ul-li--buy-img {
  align-items: center;
  display: flex;
}

.button__menu-image-2, .button__menu--close .button__menu-image-1 {
  display: none;
}

.button__menu--close .button__menu-image-2 {
  display: block;
}

.consultation__window {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background: rgba(39, 39, 39, .9);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.consultation__window--open {
  display: block;
}

.consultation__window--polotno {
  width: 288px;
  background-color: #fff;
  flex-direction: column;
  padding: 20px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.consultation__window--form {
  display: grid;
}

.consultation__window--title {
  color: #000;
  text-align: center;
  font-family: Gilroy;
  font-size: 24px;
