  border-bottom: 1px solid;
  margin-top: 10px;
  padding-bottom: 10px;
  font-family: Gilroy;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.consultation__window--knopka {
  width: 15px;
  height: 15px;
  background-color: rgba(0, 0, 0, 0);
  flex-shrink: 0;
  position: absolute;
  top: -20px;
  right: -20px;
}

@media screen and (min-width: 768px) {
  .header__suppbox {
    border: 0;
    flex-direction: row;
    margin: 0 30px 0 auto;
    padding: 0;
    display: flex;
  }

  .header__suppbox--none {
    display: none;
  }

  .menu {
    justify-content: space-evenly;
    display: flex;
  }

  .consultation__window--polotno {
    width: 690px;
    padding: 100px;
  }

  .consultation__window--title {
    font-size: 36px;
  }

  .consultation__window--text {
    font-size: 14px;
  }

  .consultation__window--button {
    margin-top: 40px;
  }

  .consultation__window--span:first-child {
    margin-bottom: 15px;
  }
}

@media screen and (min-width: 1920px) {
  .offScroll {
    overflow: auto;
  }

  .menu {
    display: flex;
  }

  .menu__ul {
    flex-direction: row;
    gap: 50px;
    margin: 0 0 0 180px;
    display: flex;
  }

  .menu__ul-li---active {
    color: #fcd54c;
    text-transform: uppercase;
    border-bottom: 4px solid #fcd54c;
    font-family: Gilroy;
    font-size: 14px;
