    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }

  .logo__button {
    margin-right: 119px;
  }

  .header__suppbox-language {
    margin-right: 53px;
  }

  .button__menu {
    display: none;
  }

  .burger {
    position: inherit;
    width: auto;
    height: auto;
    background-color: #272727;
    padding: 0;
  }

  .menu {
    background-color: rgba(252, 214, 76, 0);
    flex-direction: row;
    display: flex;
    position: relative;
  }

  .burger__open-buy {
    width: 247px;
    height: auto;
    background: #272727;
    border: 1px solid #979797;
    flex-shrink: 0;
    gap: 0;
    padding: 0;
