  }

  .menu__ul-li--down:focus > .burger__open-buy, .menu__ul-li--down:hover > .burger__open-buy {
    height: auto;
    opacity: 1;
  }

  .menu__ul-li--down:focus, .menu__ul-li--down:hover {
    height: auto;
  }

  .consultation__window--polotno {
    width: 730px;
    height: auto;
    padding: 100px;
  }

  .consultation__window--title {
    font-size: 48px;
  }

  .consultation__window--text {
    font-size: 20px;
  }

  .consultation__window--button {
    margin-top: 56px;
  }

  .consultation__window--form {
    padding-top: 46px;
  }

  .consultation__window--span:first-child {
    margin-bottom: 20px;
  }

  .consultation__window--knopka {
    width: 30px;
    height: 30px;
    flex-shrink: 0;
    top: -56px;
    right: -100px;
  }
}

